@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.login-page {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-image: var(--background);
}

.flex-parent {
  display: flex;
  flex-direction: row;
  place-content: center;
  margin-top: 5%;
}

.login-links-container {
  display: flex;
  flex-direction: column;
  margin-left:1rem
}

.login-buttons {
  font-size: 2rem;
  background-color: hsla(165, 90%, 72%, 1);
  padding: 0.9rem 1.7rem;
  color: black;
  margin: 0.5rem;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-family: 'Poppins', sans-serif;
  border: 1px solid black;
  box-shadow: inset 0 0 0 2px white
}

.login-buttons:hover {
  box-shadow: inset 0 0 0 6px #ffffff;
  background-color: transparent;
  cursor: pointer
}

.input-container {
  display: flex;
  flex-direction: column;
  margin-right: 6rem;
  margin-top:auto;
  margin-bottom: auto;
}

.login-input {
  margin: 0.5rem;
  padding: 0.8rem 1.5rem 0.8rem 1rem;
  font-size: 2rem;
  letter-spacing: 0.1rem;
  font-family: 'Source Sans Pro', sans-serif;
  width: 18rem;
  -webkit-transition: width 0.5s ease-in-out;
  transition: width 0.5s ease-in-out;
  border: none;
  border-bottom: 1px solid var(--orange);
  background-color: transparent;
  color: darkslategrey;
  outline: none
}

.login-input:focus {
  background-color: var(--orange-secundary);
}

.ranking-item {
  padding: 3rem 6rem;
  display: flex;
  flex-direction: row;
  border: 3px solid black;
  margin: 1rem;
  place-content: space-between;
  grid-gap: 3rem;
  gap: 3rem;
  background-color: rgba(255, 255, 255, 0.397);
  font-size: 1.6rem;
  justify-content: center;
  align-content: center;
}

.picture-ranking {
  margin-top: 20%;
  margin-bottom: auto;
}

.player-name {
  font-weight: 800;
}

.timer {
  color: tomato;
  font-weight: 800;
}
.settings-page {
  background-image: var(--background);
  display: flex;
  flex-direction: column;
  place-content: center;
  place-items: center;
  height: 100vh;
  text-align: center;
}

.feedback-page {
  background-image: var(--background);
  display: flex;
  flex-direction: column;
  place-content: center;
  place-items: center;
  height: 100vh;
  overflow: hidden;
  margin-top: zero
}

.feedback-text {
  font-size: 3rem;
  margin-top: 0;
  padding-top: 0;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  font-weight: 800;
  font-family: 'Poppins', sans-serif;
}

.score-container {
  font-size: 1.8rem;
  display:flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 2rem;
}

.feedback-links-container {
  font-size: 1.5rem;
  display:flex;
  flex-direction: column;
  text-align: center;
}

.feedback-buttons {
  font-size: 2rem;
  background-color: hsla(165, 90%, 72%, 1);
  padding: 0.9rem 1.7rem;
  color: black;
  margin: 0.5rem;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-family: 'Poppins', sans-serif;
  border: 1px solid black;
  box-shadow: inset 0 0 0 2px white
}

.feedback-buttons:hover {
  box-shadow: inset 0 0 0 6px #ffffff;
  background-color: transparent;
  cursor: pointer
}

.correct-answers, .final-score {
  font-weight: 800;
}
.header {
  display: flex;
  flex-direction: row;
  place-items: center;
  place-content: center;
  grid-gap: 6rem;
  gap: 6rem;
  width: 100%;
  height: 10rem;
  background-color: #CAFDBE;
  color: black;
  font-size: 1.8rem;
}

.profile-picture {
  border-radius: 50%;
  height: 6rem;
}

.name-and-pic {
  display: flex;
  flex-direction: row;
  grid-gap: 2rem;
  gap: 2rem
}

.header-score, .header-player-name {
  font-weight: 800;
}
.page-quiz {
  display: flex;
  place-items: center;
  place-content: center;
  background-image: var(--background);
  width: 100vw;
  min-height: 100vh;
}

.column {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-bottom: auto;
}

.answer-correct {
  border: 3px solid #3daa73;
  background-color: #ffafbd;
}

/* 
.answer-wrong {
  border: 3px solid red;
  color: red;
} */

.container-quiz {
  display: flex;
  flex-direction: row;
  color: black;
  place-content: center;
  place-items: center;
  width: 100%;
  margin-top: 5%;
  font-size: 2rem;
  margin-bottom: 2rem;
  word-wrap: break-word
}

.container-question {
  display: flex;
  flex-direction: column;
  place-items: center;
}
/* 
.container-timer {
  text-align: center;
  margin: 2rem;
} */

.question-text {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: black
}

.container-answers {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}

.left {
  place-items: center;
  place-content: center;
  text-align: center;
  width: 50%;
}

.right {
  width: 50%;
}

.alternative-button {
  display: inline-block;
  padding: 1rem;
  text-align: center;
  margin: 0.3rem 0.3rem 0.3rem 0;
  border-radius: 0.12rem;
  /* background-color: #3daa73; */
  /* border: 1px solid black; */
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  text-align: center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  color: black;
  width: 16rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: 50%;
}

.alternative-button:hover {
  opacity: 0.8;
  cursor: pointer
}

.btn-next:hover {
  cursor: pointer
}

.btn-next-container {
  text-align: center;
}

.btn-next {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 2rem;
  background-color: #222;
  color: white;
  border: none;
  margin-top: 2rem;
  /* margin: 0.3em 0.3em 0.3em 0; */
  border-radius: 0.12rem;
  padding: 1rem 2.5rem;
  position: fixed;
  bottom: 10%
}
.ranking-page {
  background-image: var(--background);
  display: flex;
  flex-direction: column;
  place-content: center;
  place-items: center;
  min-height: 100vh;
  text-align: center;
}

.ranking-page-empty {
  background-image: var(--background);
  display: flex;
  flex-direction: column;
  place-content: center;
  place-items: center;
  height: 100vh;
  text-align: center;
}

.ranking-list {
  list-style: none;
  text-align: center;
  padding: 0;
}

.ranking-title {
  font-family: 'Poppins', sans-serif;
  font-size: 3rem;
  letter-spacing: 0.3rem;
}

.empty-message {
  padding: 3rem 6rem;
  border: 3px solid black;
  margin-bottom: 10rem;
  background-color: rgba(255, 255, 255, 0.397);
  font-size: 2rem;
  text-align: center;
}
:root {
  --background: linear-gradient(to top, #d5dee7 0%, #ffafbd 0%, #c9ffbf 100%);;
  --green: hsla(165, 90%, 72%, 1);
  --primary-green: #c9ffbf;
  --correct-green: #3daa73;
  --orange: #f37a18;
  --pink: #ffafbd;
  --orange-secundary: rgba(255, 178, 115, 0.37);
  --dark-primary: rgb(22, 26, 37);
  --font-color: darkslategray
}

body {
  font-family: 'Source Sans Pro', sans-serif;  
}

.page-title {
  font-family: 'Poppins', sans-serif;
  font-size: 3rem;
  letter-spacing: 0.3rem;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  position: relative;
  top: 50px;
  margin: 2rem;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
}
.loader {
  color: #ffffff;
  font-size: 11px;
  text-indent: -99999em;
  margin: 55px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before,
.loader:after {
  position: absolute;
  content: '';
}
.loader:before {
  width: 5.2em;
  height: 10.2em;
  background: #0dc5c1;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.1em 5.1em;
  transform-origin: 5.1em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.loader:after {
  width: 5.2em;
  height: 10.2em;
  background: #0dc5c1;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 4.9em;
  -webkit-transform-origin: 0.1em 5.1em;
  transform-origin: 0.1em 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
